
h3,
h4 {
    margin-bottom: 15px;
}

li {
    position: relative;
    box-sizing: border-box;
    margin-top: 0.25em;
    margin-bottom: 0.25em;
    font-family: inherit;
    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    line-height: inherit;
    list-style: none none;
}

.forward-stop-name {
    color: blue;
}

.reverse-stop-name {
    color: red;
}
